import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Form } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import image from "../../images/vetsline.svg";
import { userlogin, getuser } from "../../reduxx/actions/actions";
import axios from "axios";

const Login = () => {
  const [data, setData] = useState({
    email: "",
    password: "",
  });
  const [error, setError] = useState("");
  const dispatch = useDispatch();
  const handleChange = (e) => {
    setData({ ...data, [e.target.name]: e.target.value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(userlogin(data)).catch((err) => {
      setError(err.response.data.data);
    });
  };

  return (
    <div>
      <div className="d-flex align-items-center auth px-0">
        <div className="row w-100 mx-0">
          <div className="col-lg-4 mx-auto">
            <div className="auth-form-light text-left py-5 px-4 px-sm-5">
              <div className="brand-logo">
                <img src={image} alt="logo" style={{ width: "100px" }} />
              </div>
              <h6 className="font-weight-light">
                Sign in to &nbsp;
                <span style={{ fontWeight: "bold", fontSize: "20px" }}>
                  VETSLINE
                </span>
              </h6>
              <Form className="pt-3" onSubmit={handleSubmit}>
                <Form.Group className="d-flex search-field">
                  <Form.Control
                    type="email"
                    placeholder="Email"
                    onChange={handleChange}
                    size="lg"
                    className="h-auto"
                    name="email"
                    required
                  />
                </Form.Group>
                <Form.Group className="d-flex search-field">
                  <Form.Control
                    type="password"
                    placeholder="Password"
                    size="lg"
                    onChange={handleChange}
                    className="h-auto"
                    name="password"
                    required
                  />
                </Form.Group>
                {error ? (
                  <div class="alert alert-danger" role="alert">
                    {error}
                  </div>
                ) : (
                  false
                )}
                <div className="mt-3">
                  {
                    // <Link className="btn btn-block btn-primary btn-lg font-weight-medium auth-form-btn">
                  }

                  <button
                    type="submit"
                    className="btn btn-block btn-primary btn-lg font-weight-medium"
                  >
                    Login
                  </button>
                </div>
                <div className="my-2 d-flex justify-content-between align-items-center"></div>
              </Form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
